#myFooter {
  background-color: #072846;
  color: white;
  padding-top: 30px;
}

#myFooter .footer-copyright {
  background-color: #072846;
  padding-top: 0px;
  padding-bottom: 3px;
  text-align: initial;
}

#myFooter .row {
  margin-bottom: 60px;
}

#myFooter .navbar-brand {
  margin-top: 45px;
  height: 65px;
}

#myFooter .footer-copyright p {
  margin: 60px;
  color: #ccc;
}

#myFooter ul {
  list-style-type: none;
  padding-left: 0;
  line-height: 1.7;
}

#myFooter h5 {
  font-size: 18px;
  color: white;
  font-weight: bold;
  margin-top: 30px;
}

#myFooter h2 a {
  font-size: 50px;
  text-align: center;
  color: #fff;
}

#myFooter a {
  color: #d2d1d1;
  text-decoration: none;
}

#myFooter a:hover,
#myFooter a:focus {
  text-decoration: none;
  color: white;
}

#myFooter .social-networks {
  text-align: center;
  padding-top: 30px;
  padding-bottom: 16px;
}

#myFooter .social-networks a {
  font-size: 32px;
  color: #f9f9f9;
  padding: 10px;
  transition: 0.2s;
}

#myFooter .social-networks a:hover {
  text-decoration: none;
}

#myFooter .facebook:hover {
  color: #0077e2;
}

#myFooter .google:hover {
  color: #ef1a1a;
}

#myFooter .twitter:hover {
  color: #00aced;
}

#myFooter .btn {
  color: white;
  background-color: #d84b6b;
  border-radius: 20px;
  border: none;
  width: 150px;
  display: block;
  margin: 0 auto;
  margin-top: 10px;
  line-height: 25px;
}

@media screen and (max-width: 767px) {
  #myFooter {
    text-align: center;
  }
}

/* CSS used for positioning the footers at the bottom of the page. */
/* You can remove this. */

html {
  height: 100%;
}

body {
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  -webkit-flex-direction: column;
  height: 100%;
}

.content {
  flex: 1 0 auto;
  -webkit-flex: 1 0 auto;
  min-height: 200px;
}
.card {
  width: 18rem;
  border: groove;
  background-color: #072846;
}
#myFooter {
  flex: 0 0 auto;
  -webkit-flex: 0 0 auto;
}
.cardimgtop {
  background-color: #072846;
  padding-top: 29px;
  height: 175px;
  width: 223px;
  padding-left: 21px;
}
.cardbody {
  background-color: #072846;
  border: solid;
}
.listgrouplistgroupflush,
.list-group-item {
  border-color: #072846;
}
.name {
  float: left;
  color: cornflowerblue;
}
.designation {
  float: left;
  color: cornflowerblue;
}
.listgrouplistgroupflush,
.list-group-item {
  border: none;
}
#detailsGroup {
  background-color: #072846;
  border: solid;
}
